import React from 'react';
import {Link} from 'react-router-dom'
import Dyno from '../Img/dinosaur.png';
import Boy from '../Img/boy.png';
import Book from '../Img/book.png';
import styled from 'styled-components';
import { About, Description, Image, Button } from '../style';
import { titleAnim, Fade, Img, Imag } from '../animation';
import { motion } from 'framer-motion';


const AboutSection = ()=>{
    return(
        <About>
            <Description>
                <motion.h2 variants={titleAnim} initial="hidden" animate="show">The most engaging</motion.h2>
                <motion.h2 variants={titleAnim} initial="hidden" animate="show">learning platform</motion.h2>
                <motion.h2 variants={titleAnim} initial="hidden" animate="show">for kids 3-7</motion.h2>
                <motion.p variants={Fade} initial="hidden" animate="show">Lorem ipsum dolor sit amet consectetur adipisicing elit. Eligendi vero aut necessitatibus dolores molestias et quos similique quam corrupti corporis!</motion.p>
                <Button>Get started</Button>
            </Description>
            <Image>
                <motion.img variants={Img} initial="hidden" animate="show" id="second" src={Dyno} alt="Dynosaur" />
                <motion.img variants={Img} initial="hidden" animate="show" id="third" src={Boy} alt="Boy" />
                <motion.img variants={Imag} initial="hidden" animate="show" id="first" src={Book} alt="Dynosaur" />
            </Image>
        </About>
    )
};



export default AboutSection;