import React, {useEffect, useState} from 'react';
import axios from 'axios'; 

function Axios(){
    const [weather, setWeather] = useState(null);
    const [input, setInput] = useState("");
    useEffect(() => {
        axios.get('http://api.weatherapi.com/v1/current.json?key=5931348c0f854eec85c121430221404&q=London&aqi=no')
        .then(data => {
            setWeather(data.data);
            console.log(data.data);
        })
        .catch((err) => console.log(err));
    }, []);
    const weatherInput = (e) => {
        setInput(e.target.value);
    };
    const searchWeather = () => {
        axios.get(`http://api.weatherapi.com/v1/current.json?key=5931348c0f854eec85c121430221404&q=${input}&aqi=no`)
        .then(data => {
            setWeather(data.data);
        })
    }
    return(
            <div>
            {weather && (
            <div>
                <div className="search">
                    <input onChange={weatherInput} type="text" />
                    <button onClick={searchWeather}>Search</button>
                </div>
                <div className="weather-info">
                    <h1>{weather.location.country}</h1>
                    <h2>{weather.location.regoin}</h2>
                    <div className="condition">
                        <h3>{weather.current.condition.text}</h3>
                        <img src={weather.current.condition.icon} alt="weather-icon"/>
                        <h3>{weather.current.temp_c} Celcius</h3>
                    </div>
                </div>
            </div>
            )}
        </div>
    );
};

export default Axios;