import styled from "styled-components"
import { motion } from "framer-motion";

export const About = styled(motion.div)`
    min-height:90vh;
    display:flex;
    align-items:center;
    justify-content:center;
    padding: 5rem 10rem;  
    @media (max-width: 360px){
        flex-direction: column;
        padding: 0;
    }
    @media (max-width: 600px){
        
    }
`;

export const Description = styled.div`
    padding:2rem;
    h2{
        font-size:3.5rem;
        color: #2f0080;
    }
    p{
        max-width: 35rem;
        font-size: 1.2rem;
        line-height: 1.5rem;
        margin: 3rem 0rem;
    }
    @media (max-width: 360px){
        padding:1rem;
        h2{
            font-size: 2.5rem;
        }
        p{
            font-size:1rem;
        }
    }
    @media (max-width: 600px){
        
    }
`;

export const Image = styled.div`
    
    @media (max-width: 360px){
        margin-top: 30px;
        display:flex;
        img{
            height: 100px;
        }
    }
    @media (max-width: 600px){
        
    }
`;
export const Hide = styled.div`
    overflow:hidden;
`;

export const Button = styled.button`
    height: 5rem;
    width: 12rem;
    padding: .8rem 2rem;
    background-color: #ff2200;
    border-radius: 40px;
    border: 1px solid transparent;
    font-size: 1.2rem;
    color: #fff;
    &:hover{
        background-color: #3e0080;
        font-size: 1.5rem;
        transition: 2s ease;
    }
    @media (max-width: 360px){
        height: 3rem;
        width:10rem;
        font-size:1rem;
    }
    @media (max-width: 600px){
        
    }
`;

export const Services = styled(motion.div)`
    background-color: #3e0080;
    display: flex;
    align-items: center;
    justify-content:center;
    text-align:center;
    padding: 2rem 0;
    h2{
        margin: 20px;
        font-size: 2.5rem;
        text-align: center;
        color: #fff;
    }
    @media (max-width: 360px){
        padding: 1.2rem 1rem;

        h2{
            font-size: 2rem;
        }
        p{
            font-size:1rem;
        }
    }
    @media (max-width: 600px){
        
    }
`
export const Describe = styled(Description)`
    display:flex;
    padding: 1rem;
    z-index:0;
    div{
        background-color: #fff;
        margin: .5rem .5rem;
        border-radius: 25px;
        padding: 1rem;
        h2{
            width: 10rem;
        }
        img{
            width: 300px;
            border-radius: 25px 25px 0 0;
        }
        p{
            text-align:left;
            font-weight: 500;
        }
    }
    @media (max-width: 1040px){
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        div{
            padding: 1rem;
            margin:.5rem;
            h2{
                font-size:1.5rem;
            }
        }
    }
    @media (max-width: 600px){
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 360px){
        display: block;
    }
    
`;