import React, {useState} from 'react';
import Dino from '../Img/dinosaur.png';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import {useLocation} from 'react-router-dom';
import Menu from '../Img/menu.svg';
import Exit from '../Img/exit.svg';

const Nav  = ()=>{
    const {pathname} = useLocation();
    const [choice, choiceChange] = useState(false);
    function MenuChangeHandler(){
        choiceChange(!choice);
    };
    return(
        <NavStyle>
            <LogoHead>
                <Link to="/"><img src={Dino} alt="dinosaur" /></Link>
                <Link to="/"><h2>Do<span>go</span></h2></Link>
            </LogoHead>
            <div>
                <div onClick={MenuChangeHandler} id={`${choice? "change": "menu"}`}>
                    <img src={Menu} alt="menu" />
                </div>
                <MenuBox>
                    <div className={`${choice? "changed": "exit"}`}>
                        <img onClick={MenuChangeHandler} src={Exit} alt="exit" />
                        <div id="lists">
                            <ul>
                                <li>
                                    <Link to="/teach">What We Teach</Link>
                                    <Line transition={{duration: 0.75}} initial={{width: '0%'}} animate={{width: pathname === "/teach" ? "50%" : "0%"}}/>
                                </li>
                                <li>
                                    <Link to="/playdogo"> Why PlayDogo</Link>
                                    <Line transition={{duration: 0.75}} initial={{width: '0%'}} animate={{width: pathname === "/playdogo" ? "50%" : "0%"}}/>
                                </li>
                                <li>
                                    <Link to="/contactus">Contact Us</Link>
                                    <Line transition={{duration: 0.75}} initial={{width: '0%'}} animate={{width: pathname === "/contactus" ? "50%" : "0%"}}/>
                                </li>
                            </ul>
                            <ul>
                                <li>
                                    <Link to="/login">Log in</Link>
                                    <Line transition={{duration: 0.75}} initial={{width: '0%'}} animate={{width: pathname === "/login" ? "50%" : "0%"}}/>
                                </li>
                                <li>
                                    <Link to="/">Get Started</Link>
                                    <Line transition={{duration: 0.75}} initial={{width: '0%'}} animate={{width: pathname === "/" ? "50%" : "0%"}}/>
                                </li>
                            </ul>
                        </div>
                    </div>
                </MenuBox>
            </div>
        </NavStyle>
    )
};

const NavStyle = styled.div`
    display: flex;
    justify-content: space-around;
    margin:0;
    position:sticky;
    top:0;
    background:inherit;
    #lists{
        display:flex;
    }
    a{
        text-decoration: none;
        color: black;
        font-weight: 400;
    }
    ul{
        display: flex;
        li{
            font-size: 1.2rem;
            list-style: none;
            margin: 20px;
            font-weight:600;
        }
    }
    #menu{
        display:none;
    }
    @media (max-width: 600px){
        flex-direction:column;
        padding:1rem;
        display:block;
        justify-content:center;
        ul{
            li{
                position:relative;
                font-size:1.2rem;
                margin:.2rem;
            }
        }
    }
    @media (max-width: 360px){
        padding:.8rem;
        display:flex;
        justify-content:space-between;
        flex-direction: row;
        width:96%;
        background-color:#fbfbff;
        #lists{
            display: block;
        }
        ul{
            display:block;
            li{
                font-size:1rem;
                margin:1rem 0;
                a{
                    color:#f1f1f1;
                }
                a:hover{
                    color: #ff2200;
                }
            }
        }
        #menu{
            display:block;
            height: 18px;
            width: 25px;
            margin-top:.2rem;
            cursor:pointer;
            transition: all 2s ease;
        }
        #change{
            display:none;
            height: 18px;
            margin-top:.2rem;
            cursor:pointer;
            transition: all 2s ease;
        }
    }
`;
const MenuBox = styled.div`
    @media (max-width: 360px){
        .exit{
            background-color:#36069d;
            padding: 1rem 2rem;;
            position: fixed;
            height: 100%;
            width: 80%;
            display:none;
            opacity:0;
            transition: all 5s ease;
            z-index: 3;
            right:0;
            margin-top:-20px;
        }
        ul{
            li{
                z-index:2;
            }
        }
        .changed{
            background-color:#36069d;
            padding: 1rem 2rem;;
            position: fixed;
            height: 100%;
            width: 80%;
            display:block;
            opacity:1;
            transition: all 5s ease;
            z-index: 3;
            right:0;
            margin-top:-20px;
        }
    }
`;

const LogoHead = styled.div`
    display: flex;
    margin-top: 20px;
    img{
        width: 30px;
        height: 30px;
        background-color: #82139b;
        object-fit: cover;
        border-radius: 50%;
    }
    h2{
        font-size: 2rem;
        color:#36069d;
        span{
            color: #ff2200;
        }
    }
    @media (max-width: 600px){
        justify-content:center;
        margin-top:0;
        h2{
            font-size: 2rem;
        }
    }
    @media (max-width: 360px){
        justify-content:left;
        margin-top:0;
        padding:0;
        img{
            width:25px;
            height: 25px;
        }
        h2{
            font-size: 1.8rem;
        }
    }
`
const Line = styled(motion.div)`
    height: 0.3rem;
    background: #36069d;
    width: 0%;
    bottom: -80%;
    left: 50%;
    margin: .2rem 0;
    @media (max-width: 1300px){
        left: 0;
    }
`;
export default Nav;