import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { pageAnimation, slider, sliderContainer, Move } from '../animation';
import Axios from '../components/axios';
import { useState } from 'react';
import fish2 from '../Img/fish2.png';
import fish1 from '../Img/fish1.png';
import ScrollTop from '../components/scrollTop';


const LogIn = ()=>{
    const [login, setLogIn] = useState(false);
    const [register, setRegister] = useState(true);
    function logs(){
            setLogIn(!login);
            setRegister(login);
    };
    function regist(){
            setRegister(!register);
            setLogIn(register);
    }

    // The Login and register functionality
    const admin = {
        victoria:{
            name: "victoria",
            lastName: "Olumide",
            email: "vickyOlu",
            password:"oluvictoria22"
        }
    }
    const [adminUser, setAdminUser] = useState(admin.victoria);
    const [user, setUser] = useState({name: "", email:""})
    const Login = (details) =>{
        console.log(details);
        if(details.email === adminUser.email && details.name === adminUser.name && details.password === adminUser.password){
            console.log("Logged in");
            setUser({
                name:details.name,
                email:details.email,
                password:details.password
            })
        }
        else{
            console.log("Detials do not match")
        }
    }

    const [details, setDetails] = useState({name: "", email: "", password:""});
    const submitHandler = e =>{
        e.preventDefault();
        Login(details);
    }
    return(
        <Logon variants = {pageAnimation} initial="hidden" animate="show">
            <motion.div variants={sliderContainer} initial="hidden" animate="show">
                <Frame1 variants={slider}></Frame1>
                <Frame2 variants={slider}></Frame2>
                <Frame3 variants={slider}></Frame3>
                <Frame4 variants={slider}></Frame4>
            </motion.div>
            <Log>
                {(user.email !== "")? (
                        <LoginPage>
                            <div className="streak">
                                <div className="aside">
                                    <ul>
                                        <li>Dashboard</li>
                                        <li>Profile</li>
                                        <li>Notifications</li>
                                        <li>Logout</li>
                                    </ul>
                                </div>
                                <div>
                                    <h2>Welcome back <span>{user.name}</span></h2>
                                    <Axios/>
                                </div>
                            </div>
                        </LoginPage>
                ): (
                <div>
                    <Sign>
                        <h2 onClick={()=>logs()}>Log In</h2>
                        <h2 onClick={()=>regist()}>Register</h2>
                    </Sign>
                    <MoveFish variants={Move} initial="hidden" animate="show">
                        <img src={fish2} alt="fish" />
                        <img src={fish1} alt="fish" />
                    </MoveFish>
                    <Forms>
                        <form onSubmit={submitHandler} id="form1" className={`${login?"setLog":`${register? "": "setLog"}`}`}>
                            <label className="credentials" htmlFor="name">
                                <input type="text" placeholder="First Name" required onChange={e => setDetails({...details, name:e.target.value})} value={details.name}/>
                            </label>
                            <label className="credentials" htmlFor="name">
                                <input type="email" placeholder="Email Address" required onChange={e => setDetails({...details, email:e.target.value})} value={details.email}/>
                            </label>
                            <label className="credentials" htmlFor="password">
                                <input type="password" placeholder="Password" required onChange={e => setDetails({...details, password:e.target.value})} value={details.password}/>
                            </label>
                            <label className="checks" htmlFor="ckeck">
                                Remember Me
                                <input type="checkbox" />
                            </label>
                            <label className="checks" htmlFor="ckeck">
                                Subscribe to our newsletter
                                <input type="checkbox" />
                            </label>
                            <button>Login</button>
                        </form>
                        <form action="" id="form2" className={`${register? "setLog": `${login? "": "setLog"}`}`}>
                            <label className="credentials" htmlFor="name">
                                <input type="text" placeholder="First Name" required/>
                            </label>
                            <label className="credentials" htmlFor="name">
                                <input type="text" placeholder="Last Name" required/>
                            </label>
                            <label className="credentials" htmlFor="name">
                                <input type="text" placeholder="Email Address" required/>
                            </label>
                            <label className="credentials" htmlFor="password">
                                <input type="password" placeholder="Password" required/>
                            </label>
                            <label className="credentials" htmlFor="password">
                                <input type="password" placeholder="Confirm Password" required/>
                            </label>
                            <label className="checks" htmlFor="ckeck">
                                Remember Me
                                <input type="checkbox" />
                            </label>
                            <label className="checks" htmlFor="ckeck">
                                Subscribe to our newsletter
                                <input type="checkbox" />
                            </label>
                            <button>Register</button>
                        </form>
                    </Forms>
                </div>
                )}
            </Log>
            <ScrollTop/>
        </Logon>
    )
};

const Log = styled.div`
    border: 1px solid #C6B4DB;
    border-radius: 25px;
    box-shadow: 2px 2px 2px 2px #C6B4DB;
    margin: 6rem;
    img{
        width: 60px;
    }
    form{
        display:flex;
        flex-direction: column;
        padding: 1rem;
        input{
            border: 3px solid transparent;
        }
        input:focus{
            outline: none !important;
            border-bottom: 3px solid #3e0080;
            transition: all 1s ease;
        }
        label{
            margin: 1rem;
            input{
                height: 40px;
                background-color: #C6B4DB;
                border: 0px;
            }
        }
        .credentials{
            input{
                font-size: 1rem;
                padding: .5rem .3rem;;
            }
        }
        .checks{
            display: flex;
            height: 20px;
            justify-content: space-between;
            input{
                margin-top: -10px;
                width: 1.5rem;
            }
        }
        button{
            height:50px;
            background-color: #9564cd;
            color: #2f0080;
            font-weight: 600;
            border: 0px;
            width: 50%;
            margin: 1.5rem 1rem;
            cursor:pointer;
        }

    }
    h2{
        text-align: center;
        color: #2f0080;
        font-size: 2.5rem;
        margin-top: 5rem;
    }
    
`;
const MoveFish = styled(motion.div)`
    img{
        animation-duration: 10s;
        animation-name: movingfishes;
        animation-iteration-count: infinite;
    }
    @keyframes movingfishes{
        from{
            transform:rotate(45deg);
        }
        10%{
            transform:rotate(-45deg);
        }
        20%{
            transform:rotate(0deg);
        }
        30%{
            transform:rotate(-45deg);
        }
        40%{
            transform:rotate(0deg);
        }
        50%{
            transform: rotate(-45deg);
        }
        60%{
            transform:rotate(0deg);
        }
        70%{
            transform:rotate(-45deg);
        }
        80%{
            transform:rotate(45deg);
        }
        90%{
            transform:rotate(0deg);
        }
        to{
            transform:rotate(45deg);
        }
    }
`
const Logon = styled(motion.div)`
    display:flex;
    align-items: center;
    justify-content: center;
    padding: 3rem;
    background-image: linear-gradient(180deg, #fbfbff, #3e0080);
    @media (min-width: 600px){
        form{
            padding: 12rem;
            .credentials{
                input{
                    min-width: 20rem;
                }
            }
        }
    }
`;
const LoginPage = styled.div`
    padding: 0rem;
    height: 100vh;
    width: 100%;
    h2{
        margin: 2rem;
    }
    .streak{
        display:flex;
        height: 100%;
        gap:3rem;
    }
    .aside{
        height: 95%;
        background-color:#2f0080;
        padding: 1.5rem;
        border-radius: 25px 0 0 25px;
        ul{
            li{
                list-style:none;
                margin:2rem 1rem;
                font-size: 1rem;
                color:lightblue;
                cursor:pointer;
            }
        }
    }
    @media (max-width: 600px){
        .aside{
            display:none;
        }
    }
        
`
const Sign = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    padding: .5rem;
    h2{
        width: 45%;
        font-size: 2rem;
        margin: 1rem;
        cursor:pointer;
    }
    h2:first-child{
        color: #ff2200;

    }
    @media (max-width: 600px){
        h2{
            font-size: 1.5rem;
        }
    }
`;
const Forms = styled.div`
    display: flex;
    #form1{
        transform: translateX(0%);
        opacity: 1;
        transition:all 1s ease;
    }
    #form2{
        transform: translateX(0%);
        opacity: 1;
        transition:all 1s ease;
    }
    .setLog{
        opacity: 0;
        transform: translateX(100%);
        display: none;
    }
    
`;

const Frame1 = styled(motion.div)`
    position:fixed;
    left: 0;
    top:10%;
    width: 100%;
    height: 100vh;
    background: #2f0080;
    z-index: 2;
`;
const Frame2 = styled(Frame1)`
    background: #ff2200;
`
const Frame3 = styled(Frame1)`
    background: #3e0080;
`
const Frame4 = styled(Frame1)`
    background: #ff8efb;
`
export default LogIn;