import { createGlobalStyle } from "styled-components";


const GlobalStyle = createGlobalStyle`
    *{
        margin:0;
        padding:0;
        box-sizing:'border-box';
    }
    
    body{
        background-color: #fbfbff;
        font-family: 'inter', sans-serif;
    }
    p, h2, div{
        margin: 0;
        padding: 0;
    }p{
        font-size: 1.2rem;
        line-height: 2rem;
    }
    h2{
        font-size: 1.5rem;
    }
    .show{
        display: block;
    }
    .hidden{
        display: none;
    }
`

export default GlobalStyle;