import React from 'react';
import Aquarium from '../Img/aquarium.jpg'
import {Services, Button} from '../style';
import styled from 'styled-components';

const FaqSection = ()=>{
    return(
        <Faq>
            <div id="Image">
                <div id="Aqua"><Image><img src={Aquarium} alt="aquarium" /></Image></div>
                <FaqContent>
                    <h2>How this app helps</h2>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptatem eius enim porro sint nemo, itaque neque totam similique repellat facilis dolor odio id dolorum et quae maiores consequuntur vel. Odit, saepe corporis dignissimos, vitae autem quidem maxime nesciunt rerum fugit iure ipsa corrupti sit maiores vel. Ea vitae optio ipsa!</p>
                    <Button>Get started</Button>
                </FaqContent>
            </div>
        </Faq>
    )
};

const Faq = styled(Services)`
    background-color: #fff;
    #Image{
        #Aqua{
            display: flex;
            justify-content:center;
            padding: 2rem;
        }
    }
    h2{
        color: #3e0080;
    }
    p{
        margin: 25px;
    }
    @media (max-width: 360px){
        #Image{
        #Aqua{
            padding: 0;
        }
    }
    }
`;

const FaqContent = styled.div`
    margin-top:70px;
    margin: 5rem;
    padding: 3rem;
    @media (max-width: 360px){
        padding: 0rem;
        margin: 2rem;
        p{
            text-align:justify;
        }
    }
`;
const Image = styled.div`
    width: 80%;
    height: 400px;
    img{
        object-fit: cover;
        width: 100%;
        height: 100%;
        border-radius: 25px;
    }
    @media (max-width: 360px){
        img{
            border-radius:0;
        }
    }
    @media (max-width: 600px){
        margin-top: 80px;
    }
    @media (min-width: 1040px){
        margin-top: 100px;
    }
`;
export default FaqSection;