import React from 'react';
import { motion} from 'framer-motion';
import { pageAnimation, slider, sliderContainer } from '../animation';
import styled from 'styled-components';
import Dyno from '../Img/dinosaur.png';
import ScrollTop from '../components/scrollTop';



const ContactUs = ()=>{
    return(
        <ContactStyle variants = {pageAnimation} initial="hidden" animate="show">
            <motion.div variants={sliderContainer} initial="hidden" animate="show">
                <Frame1 variants={slider}></Frame1>
                <Frame2 variants={slider}></Frame2>
                <Frame3 variants={slider}></Frame3>
                <Frame4 variants={slider}></Frame4>
            </motion.div>
            <div>
                <Title>
                    <Hide>
                        <motion.h2>Get in touch</motion.h2>
                    </Hide>
                </Title>
                <div>
                    <Hide>
                    <Social>
                        <Circle/>
                        <h3>Send Us A Message</h3>
                    </Social>
                    </Hide>
                    <Hide>
                    <Social>
                        <Circle/>
                        <h3>Send an Email</h3>
                    </Social>
                    </Hide>
                    <Hide>
                    <Social>
                        <Circle/>
                        <h3>Social Media</h3>
                    </Social>
                    </Hide>
                    <Hide>
                    <Social>
                        <Circle/>
                        <h3>Talk to Vicky O!</h3>
                    </Social>
                    </Hide>
                </div>
            </div>
            <img id="second" src={Dyno} alt="Dynosaur" />
            <ScrollTop/>
        </ContactStyle>
    )
};

const ContactStyle = styled(motion.div)`
    padding: 5rem 10rem;
    color: #353535;
    min-height: 90vh;
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    gap:20rem;
    img{
        height: 400px;
        width: 400px;
    }
    h2{
        font-size: 3rem;
        color: #2f0080;
    }
    h3{
        font-size: 2rem;
    }
    @media (max-width: 600px){
        display:block;
        padding: 2rem;
    }
    @media (max-width: 1040px){
        display:block;
        justify-content: center;
    }
`

const Title = styled.div`
    margin-bottom: 4rem;
    color: black;
    @media (max-width: 1500px){
        margin-top: 5rem;
        padding: 2rem;
        font-size: 1rem;
    }
`;

const Hide = styled.div`
    overflow: hidden;
`;

const Circle = styled.div`
    border-radius: 50%;
    width: 3rem;
    height:3rem;
    background-image: linear-gradient(45deg,  #fbfbff, #2f0080);
`;

const Social  = styled(motion.div)`
    display: flex;
    align-items: center;
    h3{
        margin: 2rem;
    }
`;

const Frame1 = styled(motion.div)`
    position:fixed;
    left: 0;
    top:10%;
    width: 100%;
    height: 100vh;
    background: #2f0080;
    z-index: 2;
`;
const Frame2 = styled(Frame1)`
    background: #ff2200;
`
const Frame3 = styled(Frame1)`
    background: #3e0080;
`
const Frame4 = styled(Frame1)`
    background: #ff8efb;
`

export default ContactUs;