import React from 'react';
import {Services} from '../style'
import styled from 'styled-components';
import Sarai from '../Img/sarai.jpg'
import Victoria from '../Img/victoria.jpg';
import { pageAnimation} from '../animation';
import { motion } from 'framer-motion';


const ParentDashboard = ()=>{
    return(
        <Parent variants={pageAnimation} initial="hidden" animate="show">
            <Dash>
                <h2>Parent Dashborad</h2>
                <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Aut tempora earum, maiores accusantium, adipisci quae aspernatur, enim facilis autem assumenda ex suscipit? Ipsam inventore maiores provident. Asperiores excepturi nisi libero.</p>
                <ParentCover>
                    <ParentInner>
                        <Dashboard>
                            <div className="first dasnotify">
                                <h2>Do<span>go</span></h2>
                                <div className="contents">
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                    <div className="contain"></div>
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                </div>
                            </div>
                            <div className="first dasnotify">
                                <h3>Welcome back Victoria!</h3>
                                <div className="contents">
                                    <div className="contain"></div>
                                    <div className="content"></div>

                                </div>
                                <ParentStyle>
                                    <Image><img src={Victoria} alt="Parent" /></Image>
                                    <div id="notify">
                                        <h4>Victoria Olumide</h4>
                                        <p>Mother</p>
                                    </div>
                                </ParentStyle>
                                <div className="contents">
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                    <div className="contain"></div>
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                </div>
                            </div>
                            <div id="last" className="dasnotify">
                                <h3>Notification</h3>
                                <ParentStyleSecond>
                                    <Image><img src={Sarai} alt="Child" /></Image>
                                    <div id="notify">
                                        <h4>Sarai Williams</h4>
                                        <p>Daughter</p>
                                    </div>
                                </ParentStyleSecond>
                                <div className="contents">
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                    <div className="contain"></div>
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                    <div className="contain"></div>
                                    <div className="content"></div>
                                    <div className="content"></div>
                                </div>
                            </div>
                        </Dashboard>
                    </ParentInner>
                </ParentCover>
            </Dash>
        </Parent>
    )
};

const Parent = styled(Services)`
    padding: 0rem 10rem 5rem 10rem;
    background-color: #fff;
    height: 100%;
    overflow:hidden;
    gap: 0;
    flex-wrap: wrap;
    @media (max-width: 360px){
        padding:1rem;
        margin: 2rem 0;
        h2{
            font-size: 2rem;
        }
        p{
            font-size:1rem;
        }
    }  
    
`;
const Image = styled.div`
img{
    width: 5%;
}
`

const ParentStyle = styled.div`
    width: 15rem;;
    display: flex;
    gap: 1rem;
    background: #fff;
    padding: 1rem;
    border-radius: 25px;
    height: 60px;
    box-shadow: 5px 0px 5px #f1f1f1;
    z-index:2;
    #notify{
        h4{
            text-align: left;
        }
        p{
            text-align: left;
            margin: 0;
        }
    }
    &:hover{
        background-color: #f1b29c;
        width:18rem;
        height: 80px;
        transition:all .2s ease;
    }
    img{
        width: 50px;
        border-radius: 50%;
    }
`;
const ParentStyleSecond = styled.div`
    width: 15rem;
    height: 60px;
    display: flex;
    gap:1rem;
    background: #fff;
    padding: 1rem;
    border-radius: 25px;
    box-shadow: 5px 0px 5px #f1f1f1;
    z-index:2;
    #notify{
        h4{
            text-align: left;
        }
        p{
            text-align: left;
            margin: 0;
        }
    }
    &:hover{
        background-color: #f1b29c;
        width:18rem;
        height: 80px;
        transition:all .2s ease;
    }
    img{
        width: 50px;
        border-radius: 50%;
    }
`
const ParentCover = styled.div`
    height:70%;
    cursor:pointer;
    @media (max-width: 360px){
        padding:0rem;
    }
`;
const ParentInner = styled.div`
    background-color: #fefefe;
    border-top: 15px solid black;
    border-left: 15px solid black;
    border-right: 15px solid black; 
    border-radius: 25px 25px 0 0;
    padding: 1rem 1rem 0 1rem;
    @media (max-width: 360px){
        padding:0;
        
    }
    
`;
const Dash = styled.div`
    background-color: #ffeeee;
    border-radius: 25px;
    margin:10rem 5rem 5rem 5rem;
    padding:10rem 10rem 0 10rem;
    height: 100%;
    flex:1;
    h2{
        color: #3e0080;
    }
    p{
        margin: 30px;
        color: #1e1c1c;
        line-height: 2rem;
    }
    @media (max-width: 360px){
        padding:0;
        width: 30rem;
    }
    @media (max-width: 600px){
        width: 30rem;
    }
`;

const Dashboard = styled.div`
    display: flex;
    justify-content: space-between;
    height: 100%;
    h2{
        font-size: 2.5rem;
        color:#36069d;
        span{
            color: #ff2200;
        }
    }
    h2:hover{
        font-size:2.8rem;
        color:#ff2200;
        span{
            color:#36069d;
        }
    }
    h3{
        font-size:2.5rem;
        margin: 1rem;
        color: #403e3e;
    }
    h3:hover{
        font-size: 2.6rem;
        color:#ff2200;
    }
    .dashnotify{
        height: 100%;
        margin: .2rem;
        padding: 0;
    }
    #last{
        height: 100%;
    }
    .first{
        display:block;
        background: #f0f4f5;
        margin: .2rem;
    }
    .contents{
        .content{
            height: 10px;
            width: 70%;
            margin: 1rem;
            background-color: #c6d5d8;
        }
        .content:hover{
            background-color: #42b8cf;
        }
        .contain{
            background-color: #c6d5d8;
            width:50%;
            height: 10px;
            margin: 1rem;
        }
        .contain:hover{
            background-color: #42b8cf;
        }
    }
    @media (max-width: 360px){
        h2{
        font-size: 2rem;
        color:#36069d;
            span{
                color: #ff2200;
            }
        }
        h3{
            font-size:1.5rem;
            margin: 1rem;
            color: #403e3e;
        }
        .first{
            display: none;
        }
    }
    @media (min-width: 360px){
        #last{
            display: none;
        }
    }
    @media (min-width: 1040px){
        #last{
            display: block;
        }
    }
`;
export default ParentDashboard;