import React from 'react';
import Image1 from '../Img/images (4).jpg'
import Image2 from '../Img/images (5).jpg'
import Image3 from '../Img/images (8).jpeg'
import Image4 from '../Img/images (9).jpeg'
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { pageAnimation, slider, sliderContainer } from '../animation';
import ScrollTop from '../components/scrollTop';



const PlayDogo = ()=>{
    return(
        <motion.div variants = {pageAnimation} initial="hidden" animate="show">
            <motion.div variants={sliderContainer} initial="hidden" animate="show">
                <Frame1 variants={slider}></Frame1>
                <Frame2 variants={slider}></Frame2>
                <Frame3 variants={slider}></Frame3>
                <Frame4 variants={slider}></Frame4>
            </motion.div>
            <div>
                <Objectives>
                    <h2>Reliable Tutors</h2>
                    <img src={Image4} alt="" />
                    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet molestias nesciunt repellendus numquam eaque. Inventore libero, doloremque saepe voluptas consequuntur mollitia ad eveniet ea vero qui rerum assumenda non magni, iure perspiciatis aliquid eligendi. Eligendi voluptas pariatur voluptatem laboriosam explicabo.</p>
                </Objectives>
                <Objectives>
                    <h2>Condusive Learning</h2>
                    <img src={Image2} alt="" />
                    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet molestias nesciunt repellendus numquam eaque. Inventore libero, doloremque saepe voluptas consequuntur mollitia ad eveniet ea vero qui rerum assumenda non magni, iure perspiciatis aliquid eligendi. Eligendi voluptas pariatur voluptatem laboriosam explicabo.</p>
                </Objectives>
                <Objectives>
                    <h2>Good Interaction Technics</h2>
                    <img src={Image3} alt="" />
                    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet molestias nesciunt repellendus numquam eaque. Inventore libero, doloremque saepe voluptas consequuntur mollitia ad eveniet ea vero qui rerum assumenda non magni, iure perspiciatis aliquid eligendi. Eligendi voluptas pariatur voluptatem laboriosam explicabo.</p>
                </Objectives>
                <Objectives>
                    <h2>Top-notch coordination</h2>
                    <img src={Image1} alt="" />
                    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Amet molestias nesciunt repellendus numquam eaque. Inventore libero, doloremque saepe voluptas consequuntur mollitia ad eveniet ea vero qui rerum assumenda non magni, iure perspiciatis aliquid eligendi. Eligendi voluptas pariatur voluptatem laboriosam explicabo.</p>
                </Objectives>
            </div>
            <Button>LEARN MORE</Button>
            <ScrollTop />
        </motion.div>
    )
};

const Objectives = styled.div`
    padding: 2rem 2rem;
    h2{
        font-size: 2.5rem;
        margin: 1.5rem;

    }
    p{
        margin: 2rem 1rem;
    }
    img{
        width:100%;
        height:150vh;
        object-fit:cover;
    }
    @media (max-width: 760px){
        img{
            max-height: 70vh;
            object-fit: contain;
        }
    }
    @media (max-width: 600px){
        img{
            max-height: 50vh;
            object-fit: contain;
        }
    }
    @media (max-width: 360px){
        img{
            max-height: 30vh;
            object-fit: contain;
        }
    }
`
const Button = styled.button`
    margin: 2rem;
    padding: .7rem;
`;
const Frame1 = styled(motion.div)`
    position:fixed;
    left: 0;
    top:10%;
    width: 100%;
    height: 100vh;
    background: #2f0080;
    z-index: 2;
`;
const Frame2 = styled(Frame1)`
    background: #ff2200;
`
const Frame3 = styled(Frame1)`
    background: #3e0080;
`
const Frame4 = styled(Frame1)`
    background: #ff8efb;
`
export default PlayDogo;