import React from 'react';
import {Describe, Services, Button} from '../style'
import styled from 'styled-components';


const EducationSection= ()=>{
    return(
        <div>
            <Education>
                <div>
                    <h2>The educational program is divided into age categofies</h2>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Sunt vero dolores libero nesciunt? Facilis, molestias voluptates necessitatibus vero hic odio temporibus qui ducimus. Veniam, similique delectus nobis illum quis nihil officiis repellendus est, praesentium et laudantium, autem a nemo nesciunt.</p>
                    <ButtonStyle>Learn more</ButtonStyle>
                </div>
            </Education>
            <Educational>
                <div>
                    <h2>Why PlayDogo</h2>
                    <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Eligendi ratione facilis possimus tenetur sequi minus, officiis vero at omnis nam!</p>
                    <Button>Learn more</Button>
                </div>
            </Educational>
        </div>
    )
};


const Education = styled(Services)`
    padding: 10rem 35rem;
    h2{
        font-size: 3rem;
        margin: 20px;
    }
    p{
        margin: 30px;
        color: #d3d0d0;
        line-height: 2rem;
    }
    @media (max-width: 360px){
        padding:1rem;
        margin: 2rem 0;
        h2{
            font-size: 2rem;
        }
        p{
            font-size:1rem;
        }
        
    }
    @media (max-width: 600px){
        padding: 2rem;
    }
    @media (max-width: 1040px){
        padding: 4rem;
    }

`

const Educational = styled.div`
    background-color: #fff;
    padding: 10rem 35rem;
    h2{
        font-size: 3rem;
        margin: 20px;
        color: #3e0080;
    }
    p{
        margin: 30px;
        color: #333030;;
        line-height: 2rem;
    }
    @media (max-width: 360px){
        padding:1rem;
        margin: 2rem 0;
        h2{
            font-size: 2rem;
        }
        p{
            font-size:1rem;
        }
        
    }
    @media (max-width: 600px){
        padding: 2rem;
    }
    @media (max-width: 1040px){
        padding: 4rem;
    }
`

const ButtonStyle = styled(Button)`
    background-color: #85c6dc;
    color: gray;
    margin: 50px 0;
    height: 5rem;
    width: 12rem;
    font-size: 1.2rem;
    @media (max-width: 360px){
        height: 3rem;
        width:10rem;
        font-size:1rem;
    }
`
export default EducationSection;