import React from 'react';
import Dyno from '../Img/dinosaur.png';
import Service1 from '../Img/code01.jpg';
import Service2 from '../Img/code02.jpg';
import Service3 from '../Img/code03.jpg';
import Service4 from '../Img/code04.jpg';
import Service5 from '../Img/code05.jpg';
import styled from 'styled-components';
import { Services,Describe,Button } from '../style';
import { motion } from 'framer-motion';
import { ShuffleLeft, ShuffleRight, ShuffleRightRight, ShuffleLeftLeft } from '../animation';



const ServicesSection = ()=>{
    return(
        <Services>
            <div>
                <h2>What subjects children will study</h2>
                <Describe>
                    <motion.div variants={ShuffleLeftLeft} initial="hidden" animate="show">
                        <img src={Service1} alt="Dinosaur" />
                        <p>Dance</p>
                    </motion.div>
                    <motion.div variants={ShuffleLeft} initial="hidden" animate="show">
                        <img src={Service2} alt="Dinosaur" />
                        <p>Career Exploration</p>
                    </motion.div>
                    <motion.div>
                        <img src={Service3} alt="Dinosaur" />
                        <p>Maths</p>
                    </motion.div>
                    <motion.div variants={ShuffleRight} initial="hidden" animate="show">
                        <img src={Service4} alt="Dinosaur" />
                        <p>Social emotional learning</p>
                    </motion.div>
                    <motion.div variants={ShuffleRightRight} initial="hidden" animate="show">
                        <img src={Service5} alt="Dinosaur" />
                        <p>Social emotional learning</p>
                    </motion.div>
                </Describe>
                <ButtonStyle>Discover</ButtonStyle>
            </div>
        </Services>
    )
};


const ButtonStyle = styled(Button)`
    background-color: #85c6dc;
    height: 5rem;
    width: 12rem;
    font-size: 1.2rem;
    margin-top:30px;
    &:hover{
        background-color:#ff2200;
        font-size: 1.5rem;
        transition: 2s ease;
    }
    @media (max-width: 360px){
        height: 3rem;
        width:10rem;
        font-size:1rem;
    }
    @media (max-width: 600px){
        margin: 3rem;
    }
    @media (min-width: 1040px){
        margin: 3rem;
    }
`

export default ServicesSection;