import React from 'react';
import Dino from '../Img/dinosaur.png';
import Aquarium from '../Img/aquarium.jpg'
import {Describe, Services} from '../style'
import styled from 'styled-components';


const GameSection = ()=>{
    return(
        <GameStyle>
            <GameInner>
                <Awesome>
                    <h2>Awesome games</h2>
                </Awesome>
                <Describe>
                    <div>
                        <h2>Planetarium</h2>
                        <img src={Aquarium} alt="Aquarium" />
                    </div>
                    <div>
                        <h2>Friendshood</h2>
                        <img src={Aquarium} alt="Aquarium" />
                    </div>
                    <div>
                        <h2>Farm</h2>
                        <img src={Aquarium} alt="Aquarium" />
                    </div>
                    <div>
                        <h2>Neighbour</h2>
                        <img src={Aquarium} alt="Aquarium" />
                    </div>
                    <div>
                        <h2>Planetarium</h2>
                        <img src={Aquarium} alt="Aquarium" />
                    </div>
                </Describe>
            </GameInner>
        </GameStyle>
    )
};


const GameStyle = styled(Services)`
    width: 100%;
    background-color: #fff;
    align-items: center;
    overflow: hidden;
    h2{
        color: #3e0080;
    }
    img{
        border-radius: 15px;
        width: 200px;
    }
    @media (max-width: 360px){
        padding:0;
        display:block;
        
    }
`;

const Awesome = styled.div`
h2{
    font-size: 3rem;
    @media (max-width: 360px){
        h2{
            font-size: 2rem;
        }
        p{
            font-size: 1rem;
        }
    }
    
}
    
`
const GameInner = styled.div`
    
`
export default GameSection;